@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.wrapper {
	&.contents_detail_view {
		.header_wrapper {
			.header_center {
				.page_title {
					text-align: left;
					font-size: variables.$fontSizeSm;

					.prod_cover {
						margin-right: 12px;
					}
				}
			}

			&.type_white {
				background: transparent;
			}
		}

		.container_wrapper {
			padding-top: 0;
			padding-bottom: 172px;
		}

		.docker_wrapper {
			&.docker_fixed {
				.floating_wrapper {
					.btn_go_top {
						bottom: 79px;
					}
				}
			}
		}
	}

	.container_wrapper {
		.prod_detail_header {
			.header_contents_inner {
				// 상품 상세 상단 thumb
				.prod_detail_thumb_wrap {
				}

				// 상품 상세 상단 view - 컨텐츠 영역
				.prod_detail_view_wrap {
					display: block;
					word-break: break-all;

					.prod_info_text {
						display: block;
						margin-top: 19px;
						color: variables.$textColorGray;
						@include mixins.font_size_sm;
						vertical-align: middle;

						&.publish_date {
							margin-top: 4px;
							color: variables.$textColorLightGray;
						}

						&.font_size_xs {
							@include mixins.font_size_xs;
						}

						.btn_publish_link {
							color: variables.$textColorLightGray;

							&:focus, &:active {
								text-decoration: underline;
							}
						}

						.date {
							font-size: variables.$fontSizeXxs;
						}

						.btn_min {
							margin-left: 5px;
							vertical-align: 0;

							@include mixins.respond-to(mobileSm) {
								margin-left: 0;
							}
						}
					}

					.prod_detail_title_wrap {
						display: block;
						position: relative;
						padding-top: 12px;
						text-align: left;

						&:before {
							content: '';
							display: block;
							position: absolute;
							left: 0;
							top: -16px;
							z-index: 1;
							width: 100%;
							height: 16px;
							background: #fff;
							border-radius: 16px 16px 0 0;
						}

						// 타이틀
						.prod_title_area {
							display: block;
							position: relative;

							.badge_list_box {
								margin-bottom: 15px;
							}

							.prod_title_box {
								display: block;
								position: relative;
								font-size: 0;

								.prod_label {
									display: inline;
									position: relative;
									padding-right: 8px;
									color: variables.$textColorPurple;
									@include mixins.font_size_xxl;
									font-weight: variables.$fontWeightRegular;

									& ~ .prod_label {
										padding-left: 4px;

										&:before {
											content: '·';
											display: block;
											position: absolute;
											left: -5px;
											top: 3px;
											font-size: variables.$fontSizeXxl;
											line-height: 1;
											font-weight: variables.$fontWeightRegular;
											letter-spacing: variables.$letterSpacing;
										}
									}
								}

								.prod_alias {
									display: inline;
									position: relative;
									padding-right: 8px;
									color: variables.$textColorBlack;
									@include mixins.font_size_xxl;
									font-weight: variables.$fontWeightMedium;
								}

								.gap {
									visibility: hidden;
									position: absolute;
								}

								// auto overflow
								.auto_overflow_contents {
									max-height: variables.$fontSizeXxlLineHeight * 4;
									overflow: hidden;
								}

								.auto_overflow_footer {
									position: absolute;
									right: 0;
									bottom: 0;
									z-index: 1;
									padding-left: 30px;
									margin-top: 0;
									height: variables.$fontSizeXxlLineHeight;
									background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 30px);
									box-sizing: border-box;

									&:before {
										content: '..';
										display: inline-block;
										margin-right: 6px;
										color: variables.$textColorBlack;
										@include mixins.font_size_xxl;
										font-weight: variables.$fontWeightBold;
										vertical-align: top;
										opacity: 0.4;
									}

									.btn_more_detail {
										margin-top: 6px;
									}
								}
							}

							.prod_title {
								display: inline;
								color: variables.$textColorBlack;
								@include mixins.font_size_xxl;
								font-weight: variables.$fontWeightBold;
							}
						}

						// 부제명 1,2, MD 부제
						.prod_desc_area {
							display: block;
							margin-top: 6px;

							.prod_desc_box {
								display: block;
								position: relative;
								font-size: 0;

								.prod_desc {
									display: inline;
									color: variables.$textColorGray;
									@include mixins.font_size_def;
									font-weight: variables.$fontWeightMedium;

									&.normal {
										color: variables.$textColorLightGray;
										font-weight: variables.$fontWeightRegular;
									}
								}

								.gap {
									display: inline-block;
									width: 1px;
									height: 10px;
									margin: 6px 10px 0;
									background-color: variables.$colorGray02;
									font-size: 0;
									vertical-align: top;
								}

								// auto overflow
								.auto_overflow_contents {
									max-height: variables.$fontSizeDefLineHeight * 3;
									overflow: hidden;
								}

								.auto_overflow_footer {
									position: absolute;
									right: 0;
									bottom: 0;
									z-index: 1;
									padding-left: 20px;
									margin-top: 0;
									height: variables.$fontSizeDefLineHeight;
									background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 20px);
									box-sizing: border-box;

									&:before {
										content: '..';
										display: inline-block;
										margin-right: 4px;
										color: variables.$textColorGray;
										@include mixins.font_size_def;
										vertical-align: top;
										opacity: 0.4;
									}

									.btn_more_detail {
										margin-top: 2px;
									}
								}
							}
						}
					}

					.prod_detail_info_area {
						display: block;
						position: relative;
						text-align: left;

						// 상품정보
						.prod_info_wrap {
							.prod_author_box {
								position: relative;
								margin-top: 15px;

								.author {
									color: variables.$textColorDarkGray;
									@include mixins.font_size_sm;
								}

								// auto overflow
								.auto_overflow_contents {
									max-height: variables.$fontSizeSmLineHeight * 3;
									overflow: hidden;
								}

								.auto_overflow_footer {
									position: absolute;
									right: 0;
									bottom: 0;
									z-index: 1;
									padding-left: 15px;
									margin-top: 0;
									height: variables.$fontSizeSmLineHeight;
									background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 15px);
									font-size: 0;

									&:before {
										content: '..';
										display: inline-block;
										margin-right: 6px;
										color: variables.$textColorDarkGray;
										@include mixins.font_size_sm;
										vertical-align: top;
										opacity: 0.4;
									}

									.btn_more_detail {
										margin-top: 2px;
									}
								}

								& ~ .btn_wrap {
									margin-top: 22px;
								}
							}

							.prod_best_box {
								display: block;
								margin-top: 13px;
								font-size: 0;

								.btn_best_link {
									@include mixins.justify_width(start, start, row, inline);
									padding: 5px 0;
									box-sizing: border-box;

									&:focus, &:active {
										.prod_best_title {
											.text {
												text-decoration: underline;
											}
										}
										.prod_best_text {
											text-decoration: underline;
										}
									}
								}

								.prod_best_title {
									display: inline-block;
									position: relative;
									max-width: 100px;
									padding-left: 23px;
									margin-right: 10px;
									@include mixins.font_size_xs;
									color: variables.$textColorBlack;
									font-weight: variables.$fontWeightBold;
									vertical-align: top;

									&:before {
										content: '';
										display: block;
										position: absolute;
										left: 0;
										top: 0;
										width: 18px;
										height: 18px;
										background: {
											position: center;
											repeat: no-repeat;
										}
										@include mixins.ratio_images('../images/ico_trophy_mok', 'png', 18px, 18px);
									}
								}

								.prod_best_text {
									display: inline-block;
									flex: 1 0;
									color: variables.$textColorDarkGray;
									@include mixins.font_size_xs;
									vertical-align: top;

									.rank {
										color: variables.$textColorBlack;
										font-weight: variables.$fontWeightBold;
									}
								}
							}

							.prod_review_box {
								@include mixins.justify_width(center);
								position: relative;
								overflow: hidden;
								margin-top: 30px;
								border: 1px solid variables.$colorGray04;
								border-radius: 10px;
								box-sizing: border-box;

								.col_review {
									@include mixins.justify_width(center, center, column);
									flex: 1;
									position: relative;
									padding: 16px 10px 21px;
									text-align: center;

									& ~ .col_review {
										&:before {
											content: '';
											display: block;
											position: absolute;
											left: 0;
											top: 50%;
											width: 1px;
											height: 34px;
											margin-top: 3px;
											background-color: variables.$colorGray04;
											@include mixins.transform(translateY(-50%));
										}
									}

									.review_score {
										display: inline-block;
										color: variables.$kyoboGreen;
										@include mixins.font_size_xl;
										font-weight: variables.$fontWeightBold;
									}

									.rating-container {
										display: inline-block;
										margin: 2px 0 2px;
									}

									.review_quotes_text {
										padding-left: 0;
										padding-top: 23px;
										margin-top: 6px;
										@include mixins.font_size_def;
										font-weight: variables.$fontWeightBold;

										&:before {
											left: 50%;
											top: 0;
											width: 16px;
											height: 16px;
											background: {
												position: center;
												repeat: no-repeat;
											}
											@include mixins.ratio_images('../images/ico_quotes_lg_mok', 'png', 16px, 16px);
											@include mixins.transform(translateX(-50%));
										}
									}

									.review_desc {
										display: inline-block;
										margin-top: 3px;
										color: variables.$textColorGray;
										@include mixins.font_size_xxs;

										.val {
											color: variables.$textColorBlack;
										}
									}
								}

								.btn_go_review {
									position: absolute;
									left: 0;
									top: 0;
									width: 100%;
									height: 100%;
									box-sizing: border-box;
								}
							}
						}

						.util_btn_wrap {
							@include mixins.justify_width(center, center);
							margin: 12px 0 9px;
							text-align: center;
							box-sizing: border-box;

							.btn_item {
								flex: 1;
								position: relative;
								padding: 0 10px;
								box-sizing: border-box;

								[class^=btn_] {
									text-align: center;
								}

								& ~ .btn_item {
									&:before {
										content: '';
										display: block;
										position: absolute;
										left: 0;
										top: 50%;
										width: 1px;
										height: 14px;
										background: variables.$colorGray04;
										@include mixins.transform(translateY(-50%));
									}
								}
							}
						}
					}

					.prod_detail_guide_area {
						display: block;
						margin-top: 10px;
						padding: 24px 0 40px;

						// 가격
						.prod_price_wrap {
							.prod_price_box {
								@include mixins.justify_width(center);

								.prod_price {
									flex: 1;
									padding-top: 3px;
									font-size: 0;

									.percent {
										display: inline-block;
										margin-right: 4px;
										color: variables.$kyoboGreen;
										@include mixins.font_size_xl;
										font-weight: variables.$fontWeightBold;
									}

									.price {
										display: inline-block;
										margin-right: 4px;
										color: variables.$textColorBlack;
										@include mixins.font_size_xl;

										.val {
											display: inline-block;
											font-weight: variables.$fontWeightBold;

											.unit {
												font-weight: variables.$fontWeightRegular;
											}
										}
									}

									.sale_price {
										display: inline-block;
										color: variables.$textColorLightGray;
										@include mixins.font_size_sm;
										vertical-align: 0;

										.val {
											display: inline-block;
											text-decoration: line-through;
										}
									}
								}

								.right_btn {
									flex: 0 auto;
								}
							}

							& ~ .prod_guide_wrap {
								margin-top: 15px;
							}

							& ~ .btn_wrap {
								margin-top: 16px;
							}
						}

						// 배송 & 혜택 & 강연/공연 정보
						.prod_guide_wrap {
							border-top: 1px solid variables.$colorGray04;
							border-bottom: 1px solid variables.$colorGray04;

							.prod_guide_box {
								padding: 19px 0 15px;

								.prod_guide_row {
									@include mixins.justify_width();
									flex-wrap: wrap;

									.prod_guide_title {
										flex: 0 auto;
										padding-right: 15px;
										color: variables.$textColorBlack;
										@include mixins.font_size_sm;
										font-weight: variables.$fontWeightBold;
									}

									.prod_guide_cont {
										flex: 1;
										margin-left: auto;
										color: variables.$textColorBlack;
										@include mixins.font_size_sm;
										text-align: right;

										.info_text {
											color: variables.$textColorBlack;
										}

										.point_text {
											display: inline-block;
											color: variables.$kyoboGreen;
											@include mixins.font_size_def;
											font-weight: variables.$fontWeightBold;
											vertical-align: top;

											.benefit_link_box {
												display: block;
												padding: 8px 15px 7px;
												margin-top: 7px;
												background: variables.$colorGray06;
												border-radius: 6px;
												text-align: center;
												font-size: 0;
												box-sizing: border-box;

												@include mixins.respond-to(mobileXs) {
													text-align: left;
												}

												.btn_link {
													display: inline-block;
													flex: 0 auto;
													padding: 3px 17px 3px 0;
													margin-right: 14px;
													position: relative;
													color: variables.$textColorGray;
													@include mixins.font_size_xs;
													font-weight: variables.$fontWeightRegular;
													text-align: center;
													vertical-align: top;
													box-sizing: border-box;

													&:after {
														content: '';
														display: block;
														position: absolute;
														right: 0;
														top: 50%;
														width: 1px;
														height: 9px;
														margin-top: -1px;
														background-color: variables.$colorGray01;
														@include mixins.transform(translateY(-50%));
													}

													&:last-child {
														padding-right: 0;
														margin-right: 0;

														&:after {
															display: none;
														}
													}
												}
											}

											.tooltip_wrap {
												margin-left: 7px;

												.tooltip_info_list {
													.tooltip_info_item {
														.title_wrap {
															margin-bottom: 0;

															.btn_tooltip_sm {
																margin: 2px 0 0 3px;
															}
														}

														.info_cont {
															margin-top: 2px;

															[class^=bul_item_] {
																.cont_title,
																.cont_desc {
																	display: inline-block;
																	vertical-align: top;
																	box-sizing: border-box;
																}

																.cont_title {
																	float: left;
																	width: calc(100% - 80px);
																}

																.cont_desc {
																	float: right;
																	width: 80px;
																	padding-left: 5px;
																	text-align: right;
																}

																& + [class^=bul_item_] {
																	margin-top: 4px;
																}

																@include mixins.clearfix;
															}
														}
													}
												}
											}
										}

										.delivery_guide_text {
											display: inline-block;
											vertical-align: top;

											.fc_spot {
												font-weight: variables.$fontWeightBold;
											}

											.delivery_type {
												display: inline-block;
												margin-bottom: 6px;
												font-weight: variables.$fontWeightMedium;
											}

											.date {
												font-weight: variables.$fontWeightBold;
											}

											.btn_tooltip_sm {
												margin: 2px 0 0 3px;
											}
										}

										.prod_info_text {
											margin-top: 0;

											.btn_min {
												@include mixins.respond-to(mobileSm) {
													margin-left: 5px;
												}
											}
										}

										.info_text_box {
											text-align: left;

											&.size_sm {
												margin: 2px 0 3px;
											}

											.info_text {
												&.font_size_xxs {
													color: variables.$textColorLightGray;
												}

												&.font_size_xxs {
													~ .info_text.font_size_xxs {
														margin-top: 6px;
													}
												}
											}

											.btn_more_view_sm {
												margin-top: 8px;
											}
										}
									}

									& ~ .prod_guide_row {
										margin-top: 8px;
									}
								}

								& ~ .prod_guide_box {
									border-top: 1px solid variables.$colorGray04;
								}

								&.type_list {
									padding: 17px 0 15px;

									.prod_guide_row {
										& ~ .prod_guide_row {
											margin-top: 14px;
										}
									}
								}
							}

							& ~ .prod_status_box {
								margin-top: 18px;
							}
						}

						// 알림신청
						.alarm_apply_swiper_wrap {
							@include mixins.justify_width(center);
							padding-top: 18px;
							box-sizing: border-box;

							.swiper-container {
								flex: 1 0;

								.swiper-wrapper {
									.swiper-slide {
										width: 100%;
										min-height: 19px;
									}
								}
							}

							.prod_alarm_text {
								display: inline-block;
								flex: 0 auto;
								color: variables.$textColorGray;
								@include mixins.font_size_xs;
								font-weight: variables.$fontWeightRegular;
								vertical-align: top;
							}

							.right_area {
								margin-left: 10px;
							}
						}

						// 판매 상태
						.prod_status_box {
							display: block;
							width: 100%;
							padding: 20px 19px 16px;
							background-color: #fff;
							border: 1px solid variables.$colorGray04;
							border-radius: 10px;
							text-align: center;
							box-sizing: border-box;

							.prod_status_text {
								display: inline-block;
								position: relative;
								padding: 0 18px;
								color: variables.$textColorGray;
								@include mixins.font_size_sm;

								.status {
									font-weight: variables.$fontWeightMedium;
								}

								&:before, &:after {
									content: '';
									display: block;
									position: absolute;
									width: 10px;
									height: 9px;
									background: {
										position: center;
										repeat: no-repeat;
									}
									@include mixins.ratio_images('../images/ico_quotes_gray_mok', 'png', 10px, 9px);
								}

								&:before {
									top: 3px;
									left: 0;
								}

								&:after {
									top: 2px;
									right: 0;
									@include mixins.transform(rotate(180deg));
								}
							}

							& ~ .info_text_box {
								margin-top: 10px;
							}
						}
					}
				}
			}
		}

		.tab_wrap {
			&.prod_detail_body {
				> .tab_list_wrap {
					.tab_list_inner {
						&.sps {
							position: relative;
							padding: 0 20px;

							.sps_inner {
								height: 100%;

								.tabs {
									height: 100%;

									.tab_item {
										.tab_link {
											&:after {
												bottom: -1px;
											}
										}
									}
								}
							}

							&.sps-blw {
								position: fixed;
								width: calc(100% - 40px);
								height: 48px;

								&:after {
									content: '';
									position: absolute;
									left: 0;
									bottom: 0;
									width: 100%;
									height: 1px;
									background-color: variables.$colorGray03;
								}
							}
						}
					}

					.tabs {
						justify-content: space-between;

						.tab_item {
							flex: initial;
							height: 48px;

							.tab_link {
								padding: 0;
							}
						}
					}
				}
			}
		}

