							@include mixins.multi_ellipsis(22px, 2, false);
						}

						.text_period {
							color: variables.$textColorGray;
							@include mixins.font_size_xs;
						}
					}
				}
			}

			.auto_overflow_wrap {
				&.active {
					margin-top: 24px;
				}

				.auto_overflow_contents {
					max-height: 0;
				}
			}
		}
	}

	// 추천 이벤트
	.recommend_event {
		.recommend_event_list {
			@include mixins.justify_width;
			flex-wrap: nowrap;

			.recommend_event_item {
				width: calc(100vw - 40px);
				margin-left: 12px;
				flex: none;

				&:first-child {
					margin-left: 0;
				}

				a {
					display: block;
					width: 100%;
					overflow: hidden;
					border-radius: 16px;

					img {
						display: block;
						width: 100%;
					}
				}
			}
		}
	}

	// 패키지
	.product_package {
		.title_prod_package {

			.title {
				@include mixins.font_size_sm;
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;
				@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 2, false);
			}

			.info_price {
				margin-top: 6px;

				[class^='price_'] {
					display: inline-block;
					@include mixins.font_size_md;
				}

				.price_dc {
					font-weight: variables.$fontWeightBold;
					color: variables.$textColorGreen;
					font-family: variables.$fontEng;
				}

				.price_amount {
					font-weight: variables.$fontWeightBold;
					color: variables.$textColorBlack;

					strong {
						font-weight: variables.$fontWeightBold;
						color: variables.$textColorBlack;
						font-family: variables.$fontEng;
					}
				}

				.price_amount_before {
					position: relative;
					@include mixins.font_size_sm;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorLightGray;

					strong {
						font-weight: variables.$fontWeightRegular;
						color: variables.$textColorLightGray;
						font-family: variables.$fontEng;
					}

					&:before {
						content: '';
						display: block;
						position: absolute;
						top: 50%;
						left: 0;
						width: 100%;
						height: 1px;
						background: variables.$textColorLightGray;
						transform: translateY(-50%);
					}
				}

				.price_point {
					@include mixins.font_size_sm;
					font-weight: variables.$fontWeightBold;
					color: variables.$textColorGray;
					font-family: variables.$fontEng;
				}
			}
		}

		.list_package {
			.thumbnail_item:first-child {
				position: relative;
				padding-right: 36px;

				&:before {
					content: '';
					position: absolute;
					right: 2px;
					top: 50%;
					display: block;
					width: 24px;
					height: 24px;
					margin-top: -12px;
					background: {
						position: 50%;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/ico_plus_product_mok', 'png', 24px, 24px);
				}
			}
		}

	}

	// 키워드 Pick
	.product_keyword_pick {
		.title_wrap {
			.tooltip_wrap {
				margin-left: 6px;
			}
		}

		.tab_wrap {
			.tab_list_wrap {
				margin-bottom: 20px;
			}
		}

		.keyword_pick_list {

		}
	}

	// 중간 큰 배너
	.detail_middle_banner {
		a {
			display: block;
			width: 100%;

			img {
				display: block;
				width: 100%;
			}
		}
	}

	// 이 책의 상품구성
	.book_config {
		.tbl_type_stack_wrap {
			display: block;
			width: 100%;
			box-sizing: border-box;
			border-top: 1px solid variables.$textColorBlack;
			border-bottom: 1px solid variables.$colorGray04;

			.list_stack {
				.item_stack {
					position: relative;
					min-height: 135px;
					box-sizing: border-box;
					padding: 20px 4px;
					border-top: 1px solid variables.$colorGray04;

					&:first-child {
						border-top: none;
					}

					.title_item {
						position: relative;
						@include mixins.justify_width;
						color: variables.$textColorBlack;

						.right_area {
							margin-left: auto;
							flex-shrink: 0;
							@include mixins.justify_width(center, start, row);
						}

						.btn_text_link {
							padding-right: 6px;
							color: inherit;

							.text {
								text-decoration: underline;
							}
						}
					}

					.info_text_row {
						.is_blind {
							position: absolute;
							width: 1px;
							height: 1px;
							margin: -1px;
							overflow: hidden;
							clip-path: polygon(0 0, 0 0, 0 0);
						}

						&.listing_array {
							.info_text {
								position: relative;
								display: inline-block;
								padding-left: 6px;
								margin-top: 0px;

								&:before {
									content: '';
									position: absolute;
									left: 2px;
									top: 5px;
									display: block;
									width: 2px;
									height: 2px;
									border-radius: 50%;
									background-color: variables.$textColorLightGray;
								}

								&:first-child {
									padding-left: 0px;

									&:before {
										display: none;
									}
								}
							}
						}

						&.author {
							margin-top: 6px;
						}

						&.pub {
							margin-top: 8px;
						}

						&.prod_details {
							margin-top: 2px;
						}
					}
				}
			}

		}

		.auto_overflow_wrap {
			.auto_overflow_contents {
				max-height: 0px;

				.tbl_type_stack_wrap {
					border-top: none;
				}
			}
		}
	}

	// 기본정보 + 상세정보
	.basic_info, .details_info {
		.tbl_row_wrap {
			tbody {
				tr {
					td {
						.tbl_btn_wrap {
							margin-top: 6px;
						}

					}
				}
			}

			.kc_text {
				color: variables.$textColorBlack;
				margin-top: 10px;

				&.kc_title_text {
					font-weight: variables.$fontWeightMedium;
				}

				&.kc_mark_wrap {
					@include mixins.justify_width(center, start, row);
				}

				.ico_kcmark {
					display: inline-block;
					width: 18px;
					height: 28px;
					@include mixins.ratio_images('/assets/common/images/ico_kcmark_mok', 'png', 18px, 28px);
					vertical-align: middle;
					margin-right: 6px;
				}
			}
		}
	}

	// 작가정보
	.writer_info {

		> .auto_overflow_wrap {

			&.active {
				margin-top: 20px;
			}

			.auto_overflow_contents {
				max-height: 0;
			}
		}

		.writer_profile {
			width: 70px;
			height: 70px;
			overflow: hidden;
			margin-bottom: 16px;
			border-radius: 50%;

			img {
				display: block;
				width: 100%;
				height: 100%;
			}
		}

		.title_wrap.has_btn {
			margin-bottom: 6px;
		}

		.view_details_author {
			margin-bottom: 20px;
		}

		.writer_field_box {
			.writer_field_list {
				@include mixins.clearfix;
				margin: -3px;

				.writer_field_item {
					float: left;
					padding: 3px;

					[class^='btn_'] {
						background-color: #fff;

						.gap {
							display: inline-block;
							width: 5px;
							font-size: 0;
							margin: 0 4px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							vertical-align: top;
							@include mixins.ratio_images('/assets/common/images/ico_field_gap_mok', 'png', 5px, 9px);
						}
					}
				}
			}
		}

		.writer_info_box {
			margin-top: 14px;

			.auto_overflow_wrap {

				/*
				.auto_overflow_footer {
					display: block;
					margin-top: 12px;
				}
				*/

				.auto_overflow_contents {
					max-height: 65px;

					.auto_overflow_inner {
						@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 3, false);
					}

				}

				&.active {
					.auto_overflow_contents {
						.auto_overflow_inner {
							@include mixins.clear_multi_ellipsis;
						}
					}
				}
			}
		}

		.round_gray_box {
			.scroll_horizontal_wrap {
				margin-top: 24px;
				margin-right: -24px;

			}
		}
	}

}

.keyword_pick_list, .writer_book_list {
	@include mixins.justify_width(center, start, row);

	.btn_more_pick {
		position: relative;
		display: block;
		padding: 31px 30px 0;
		text-align: center;
		font-weight: variables.$fontWeightRegular;
		color: variables.$textColorLightGray;
		white-space: nowrap;

		&:before {
			content: '';
			position: absolute;
			left: 50%;
			top: 0;
			display: block;
			width: 23px;
			height: 23px;
			margin-left: -12.5px;
			border: 1px solid variables.$colorGray02;
			border-radius: 50%;
			background: {
				color: #fff;
				position: 50%;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_more_pick_mok', 'png', 9px, 9px);
		}
	}
}

// 상품상세 추천영역 팝업
.dialog_wrapper .ui-dialog.prod_recommend {
	height: calc(100% - 134px) !important;

	.dialog_wrap {
		flex: 1;

		.dialog_contents {
			position: relative;
			margin: {
				left: 0;
				right: 0;
				bottom: 0 !important;
			}

			&.show_all_list {
				.prod_recommend_list_wrap {
					display: none;
				}

				.prod_recommend_nav_box {
					display: block;
				}
			}
		}
	}

	.prod_recommend_list_wrap {
		padding: 0 20px;
		height: 100%;

		.btn_show_all_list {
			position: absolute;
			right: 20px;
			z-index: 2;

			.ico_view_list_dot {
				display: inline-block;
				width: 12px;
				height: 12px;
				margin-right: 5px;
				@include mixins.ratio_images('../images/ico_view_list_dot_mok', 'png', 12px, 12px);
			}
		}

		.swiper-container {
			height: 100%;

			.swiper-wrapper {
				height: 100% !important;

				.swiper-slide {
					height: 100%;
				}
			}
		}

		.prod_recommend_box {
			height: 100%;
			@include mixins.justify_width(start, start, column);

			.prod_recommend_title {
				@include mixins.font_size_lg;

				a {
					position: relative;
					display: inline-block;
					padding-right: 13px;

					&:after {
						content: '';
						position: absolute;
						display: inline-block;
						width: 8px;
						height: 12px;
						right: 0;
						top: 7px;
						@include mixins.ratio_images('../images/ico_recommend_arw_mok', 'png', 8px, 12px);
					}
				}
			}

			.prod_recommend_list {
				width: 100%;
				flex: 1;
				margin-top: 25px;
				padding-bottom: 100px;
				overflow: {
					x: hidden;
					y: auto;
				}

				//min-height: calc(100% - 134px);
			}
		}

		.prod_recommend_footer {
			position: absolute;
			width: 100%;
			height: 80px;
			left: 0;
			bottom: 0;
			background: rgba(0, 0, 0, .9);
			z-index: 3;
		}
	}

	.prod_recommend_nav_box {
		position: absolute;
		display: none;
		width: 100%;
		height: 100%;
		padding: 0 20px 30px;
		background: #fff;
		box-sizing: border-box;
		top: 0;
		left: 0;
		z-index: 4;
		text-align: right;

		.btn_close_all_list {
			color: #fff;
			background: #292929;
			border-color: #292929;
			right: 20px;

			.ico_close_layer {
				display: inline-block;
				width: 12px;
				height: 12px;
				margin-right: 5px;
				@include mixins.ratio_images('../images/ico_close_white_mok', 'png', 12px, 12px);
			}
		}

		.prod_recommend_slider_list {
			text-align: left;

			button {
				background: #fff;
				font-size: variables.$fontSizeLg;
				line-height: 54px;

				&.active, &:focus {
					text-decoration: underline;
					font-weight: variables.$fontWeightBold;
				}
			}
		}
	}
}